.style-navsin {
  background-color: rgb(10, 168, 168);
}
.style-navsin .flex-nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 15px;
  padding-right: 15px;
}
.style-navsin .flex-nav .logo {
  font-size: 25px;
  letter-spacing: 2px;
  font-weight: bold;
  color: white;
}
.style-navsin .flex-nav .mode {
  border: 2px solid black;
  padding: 7px;
  font-size: 20px;
  font-weight: bold;
  color: white;
  letter-spacing: 3px;
  border-radius: 10px;
}
.style-navsin .flex-nav .mode:hover {
  background-color: black;
  transition: all 0.3s ease-in;
}
.style-navsin .flex-nav .buttons {
  display: flex;
  margin-top: 7px;
}
.style-navsin .flex-nav .buttons div {
  margin-left: 10px;
  text-transform: capitalize;
}
.style-navsin .flex-nav .buttons div a {
  color: white;
  text-decoration: none;
  font-size: 20px;
  letter-spacing: 2px;
  padding: 4px;
}

@media (max-width: 280px) {
  .style-navsin .flex-nav {
    zoom: 0.6;
  }
}
@media (max-width: 310px) {
  .style-navsin .flex-nav {
    zoom: 0.7;
  }
}
@media (min-width: 311px) {
  .style-navsin .flex-nav {
    zoom: 0.7;
  }
}
@media (min-width: 320px) {
  .style-navsin .flex-nav {
    zoom: 0.8;
  }
}
@media (min-width: 370px) {
  .style-navsin .flex-nav {
    zoom: 0.9;
  }
}
@media (min-width: 700px) {
  .style-navsin .flex-nav {
    zoom: 1;
  }
}/*# sourceMappingURL=Stylesing.css.map */