.style-profile-fixed-loding {
  position: fixed;
  left: 0px;
  top: 0px;
  bottom: 0px;
  right: 0px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.style-profile-fixed-loding .loade {
  width: 150px;
  height: 150px;
  border-left: 3px solid red;
  border-top: 3px solid green;
  border-bottom: 3px solid green;
  border-right: 3px solid orange;
  border-radius: 50%;
  -webkit-animation-name: loade;
          animation-name: loade;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-iteration-count: 200;
          animation-iteration-count: 200;
  transform: rotate(0deg);
}
.style-profile-fixed-loding .loade p {
  font-size: 25px;
  padding-top: 40px;
  display: block;
  text-align: center;
}

@-webkit-keyframes loade {
  0% {
    transform: rotate(20deg);
  }
  10% {
    transform: rotate(100deg);
  }
  20% {
    transform: rotate(200deg);
  }
  50% {
    transform: rotate(300deg);
  }
  80% {
    transform: rotate(500deg);
  }
  100% {
    transform: rotate(700deg);
  }
}

@keyframes loade {
  0% {
    transform: rotate(20deg);
  }
  10% {
    transform: rotate(100deg);
  }
  20% {
    transform: rotate(200deg);
  }
  50% {
    transform: rotate(300deg);
  }
  80% {
    transform: rotate(500deg);
  }
  100% {
    transform: rotate(700deg);
  }
}
.style-profile {
  position: relative;
}
.style-profile .flex-profile {
  display: flex;
  justify-content: space-between;
  position: relative;
  height: 100vh;
  width: 100%;
}
.style-profile .flex-profile .avtar-acount {
  background-color: rgb(201, 19, 25);
  width: 300px;
