
.style-profile-fixed-loding
{
    position:fixed;
    left:0px;
    top:0px;
    bottom:0px;
    right:0px;
    
    display:flex;
    justify-content:center;
    align-items:center;
    .loade
    {
        width:150px;
        height: 150px;
        border-left:3px solid red;
        border-top:3px solid green;
        border-bottom:3px solid green;
        border-right:3px solid orange;
        border-radius:50%;
        animation-name: loade;
        animation-duration: 1s;
        animation-iteration-count:200;
        transform:rotate(0deg);
        
        & p {
            font-size:25px;
            padding-top:40px;
            display:block;
            text-align:center;
        }
    }
}
.style-java
{
    h1
    {
        color:green;
    }
}