.active
{
    border:3px solid rgb(11, 223, 64);
    
}
.none
{
    display:none;
}
.block
{
    display:block;
}
.navpar-pages
{
    
    background-color:rgb(43, 23, 221);
    padding:10px;
    
   
    .navpar
    {
        display:flex;
        justify-content:space-between;
        font-size: 22px;
        
        p 
        {
            color:white;
            letter-spacing:2px;
            
        }
        .logo
        {
        font-weight:bold;
        text-transform: capitalize;
        animation-name:logo;
        animation-duration:0.6s;
        
        
        }
        .but-dark
        {
            border:1px solid white;
            border-radius:20px;
            padding-left:7px;
            padding-right:7px;
            padding-top:4px;
            padding-bottom: 4px;
            &:hover
            {
                box-shadow:2px 2px 2px 3px chartreuse;
                transition:all 0.3s ease-in-out
            }
            &:active
            {
                padding:7px;

            }
        }
        .flex-buttons
        { 
            display:flex;
            &>div 
            {
                margin-left:10px;
                margin: right 10px;
                
            }
            & a{
                text-decoration:none;
                color:white;
                padding-bottom:5px;
                padding-left:12px;
                padding-right: 10px;
                border-radius:14px;
            }
            .singout{
            & p{
                border:1px solid black;
                padding:5px;
                &:hover{
                    background-color:orange;
                }
            }}


        }
        .navpar-mobile
        {
            display:none;
            border:1px solid white;
            
            padding:1px 10px 1px 10px;
            border-radius: 10px;
            position:relative;
            .menue
            {
                background-color:rgb(199, 194, 194);
                position:absolute;
                right:2px;
                top:45px;
                z-index: 2;
                border-radius: 15px;
                & div 
                {
                    
                    padding:10px;
                    
                    p 
                    {
                        a {
                        color:rgb(17, 1, 1);
                        text-decoration: none;
                        
                        padding-left:5px;
                        padding-right:5px;
                        border-radius: 7px;
                        }
                        
                        
                    }
                }
                .singout{
                    & p{
                        border:1px solid black;
                        padding:5px;
                        &:hover{
                            background-color:orange;
                        }
                    }}
                
            }
        }
    }
}



@keyframes logo
{
    to{padding-left:40px}
    from{padding-left:0px}
}
/* start resbonsive navpar */
@media (max-width:550px)
{
    .navpar-pages
    {
        .navpar-mobile
        {
            display:block !important;
        }
        

    }
    .flex-buttons
    {
        display:none !important;
    }
}
/* ends responsive navpar */