.style-profile-fixed-loding
{
    position:fixed;
    left:0px;
    top:0px;
    bottom:0px;
    right:0px;
    
    display:flex;
    justify-content:center;
    align-items:center;
    .loade
    {
        width:150px;
        height: 150px;
        border-left:3px solid red;
        border-top:3px solid green;
        border-bottom:3px solid green;
        border-right:3px solid orange;
        border-radius:50%;
        animation-name: loade;
        animation-duration: 1s;
        animation-iteration-count:200;
        transform:rotate(0deg);
        
        & p {
            font-size:25px;
            padding-top:40px;
            display:block;
            text-align:center;
        }
    }
}

@keyframes loade{
    0%{transform:rotate(20deg)}
    10%{transform:rotate(100deg)}
    20%{transform:rotate(200deg)}
    50%{transform:rotate(300deg)}
    80%{transform:rotate(500deg)}
    100%{transform:rotate(700deg)}
}

.style-profile
{

position:relative;
.flex-profile
{ 
    
    display:flex;
    justify-content:space-between;
    position:relative;
    height:100vh;
    width:100%;
    .avtar-acount
    {
        background-color:rgb(201, 19, 25);
        width:300px;
        min-width:300px;
        
        display:flex;
        flex-flow:column nowrap;
        align-items:center;
        
        .acount-img
        {
            border:3px solid orange;
            width:160px;
            height:160px;
            border-radius:50%;
            margin-bottom:50px;
            margin-top:120px;

        }
        .contentt p 
        {
            font-size: 30px;
            letter-spacing: 2px;
            color:white;
            
        }
    }
   .content-acount
   {
    
    width:100%;
    background-color: rgb(111, 161, 205);
    padding:20px;
    
    
    & p 
    {
        font-size:30px;
        letter-spacing: 2px;
        text-transform:uppercase;
        padding-bottom:40px;
    }
    .information-acount
    {
        width:100%;
        border-bottom:2px solid rgb(136, 132, 132);
        padding-bottom:15px;
        & table 
        {
            width:100%;
            font-size:25px;
            line-height:50px;
            .title
            {
                text-transform: capitalize;
                letter-spacing: 2px;
                font-weight: bold;
                
            }
            .delete-acount
            {
                background-color:red;
                padding:15px;
                font-size:20px;
                letter-spacing: 2px;
                color:white;
                border:1px solid white;
                margin:15px;

            }
        }
    }
    
   }

}
}

@media (max-width:300px)
{
    .style-profile
    {
        .flex-profile
        {
            
            display:block;
            .avtar-acount
            {
                zoom:0.6;
                width:100%;
                height:600px;
            }
            .content-acount
            {
                zoom:0.6;
                height:100vh;
            }
            
        }
    }
}


