.style-profile-fixed-loding
{
    position:fixed;
    left:0px;
    top:0px;
    bottom:0px;
    right:0px;
    
    display:flex;
    justify-content:center;
    align-items:center;
    .loade
    {
        width:150px;
        height: 150px;
        border-left:3px solid red;
        border-top:3px solid green;
        border-bottom:3px solid green;
        border-right:3px solid orange;
        border-radius:50%;
        animation-name: loade;
        animation-duration: 1s;
        animation-iteration-count:200;
        transform:rotate(0deg);
        
        & p {
            font-size:25px;
            padding-top:40px;
            display:block;
            text-align:center;
        }
    }
}

.style-singin
{
    .absolute-form
    {
        
        width:300px;
        max-width:100%;
        position:absolute;
        left:50%;
        top:30%;
        transform:translate(-50%);
        
        & form
        {
            & h2 
            {
                display:block;
                text-align:center;
                letter-spacing:2px;
                text-transform: capitalize;
                margin-bottom:60px;
            }
            width:100%;
            & input 
            {
                width:100%;
                
                padding:10px;
                border:none;
                outline:none;
                border:1px solid black;
                font-size:20px;
                letter-spacing:3px;
                border-radius:10px;
            }
            & button
            {
                background-color:aqua;
                width:100%;
                border:none;
                outline:none;
                padding:10px;
                font-size:20px;
                font-weight: bold;
                text-transform: capitalize;
                border-radius:10px;
            }
            & p 
            {
                font-size: 20px;
                
            }
        }

    }
}.style-veremail
{
    display:flex;
    flex-flow:column nowrap;
    justify-content: center;
    align-items: center;
    width:100%;
    height: 100vh;
    .flex{
        
        width:100%;
        padding:20px;
        display: flex;
        flex-flow:column nowrap;
        align-items: center;
        & p 
    {
        
        
        
        font-size:20px;
        letter-spacing: 2px;
        font-weight:bold;
    }
    & button 
    {
        
        
        background-color:red;
        color:white;
        outline:none;
        padding:10px;
        border:none;
        border-radius:15px;
        font-size:22px;
        &:focus{
            border:2px solid green;
            
        }
        &:hover
        {
            transform:scale(1.2);
        }
    }
    }
    
}