
/* sass : mixins & varible */
@import '../sass/mixins/mixins.scss';
@import '../sass/varibles/varibles.scss';
/* ---------------------- */


.light
{
    background-color: white;

}
.dark
{
    background-color: black;
    color:white;
}
.orange
{
    background-color: orange;
}
.green
{
    background-color: greenyellow;
}
.none{
    display: none;
}
.block
{
    display: block;
}

.style-home
{
    
    .stings
    {
        display:flex;
        flex-flow:column nowrap;
        justify-content:center;
        align-items:center;
        border:1px solid red;
        .menue
        {
            border:1px solid red;
            position:relative;
            background-color:orange;
            font-size:20px;
            font-weight:bold;
            text-align:center;
            margin-bottom:15px;
            &:hover{
                background-color: aqua;
            }
            .open-menue
            {
                background-color:red;
                padding:15px;
                font-size:20px;
            }
        }
        & button 
        {
            margin:15px;
            padding-left:20px;    
            padding-right:20px;
            padding:10px;
            font-size:17px;
            outline:none;
            border:1px solid black;
            font-weight:bold;
            color:black;
            border-radius: 10px;
          &:hover
          {
            background-color:aliceblue;
          }
        }
        /* style darkmode */
        .them-mode
        {
    
            display:flex;
            justify-content:space-between;
            
            & span{
                margin-left:16px;
                margin-right:16px;
                font-size:20px;
                
            }
            .contrl
            {
                background-color: rgb(189, 186, 186);
                position:relative;
                width: 200px;
                height:40px;
                overflow:hidden;
                border-radius:25px;
                display: flex;
                justify-content: space-between;
                padding-top:5px;
                padding-bottom:5px;
            
                .but-toggle
                {
                    background-color:white;
                    width:70px;
                    position:absolute;
                    height:90%;
                    top:2px;
                    border-radius:25px;
                    margin-left:5px;
                    left:1px;
                    transition: all 0.3s ease-in-out;
                    .text-themth
                    {
                        display:block;
                        text-align: center;
                        font-size:20px;
                    }
                }
            }
        }
        /* -----ends ----*/
    }
}
