.style-navsin
{
    background-color:rgb(10, 168, 168);
.flex-nav
{
    width:100%;
    
    display:flex;
    justify-content: space-between;
    padding-top:15px;
    padding-bottom:15px;
    padding-left:15px;
    padding-right:15px;
    .logo
    {
        font-size:25px;
        letter-spacing:2px;
        font-weight:bold;
        color:white;
    }
    .mode
    {
        border:2px solid black;
        padding:7px;
        font-size:20px;
        font-weight: bold;
        color:white;
        letter-spacing: 3px;
        border-radius: 10px;
        &:hover
        {
            background-color:black;
            transition:all 0.3s ease-in;
        }
    }
    .buttons
    {
        display:flex;
        margin-top:7px;
        & div 
        {
            margin-left:10px;
            text-transform:capitalize;
            & a{
                color:white;
                text-decoration: none;
                font-size:20px;
                letter-spacing: 2px;
                padding:4px;
            }
        }
    }
}
}  
@media (max-width:280px)
{
    .style-navsin
    {
        .flex-nav
        {
            zoom:0.6;
        }
    }

}
@media (max-width:310px)
{
    .style-navsin
    {
        .flex-nav
        {
            zoom:0.7;
        }
    }

}

@media (min-width:311px)
{
    .style-navsin
    {
        .flex-nav
        {
            zoom:0.7;
        }
    }

}
@media (min-width:320px)
{
    .style-navsin
    {
        .flex-nav
        {
            zoom:0.8;
        }
    }

}
@media (min-width:370px)
{
    .style-navsin
    {
        .flex-nav
        {
            zoom:0.9;
        }
    }

}
@media (min-width:700px)
{
    .style-navsin
    {
        .flex-nav
        {
            zoom:1;
        }
    }

}
